import { h, render, Component } from 'preact';

class AddTimeSlot extends Component {
    constructor() {
        super();

        this.state = {
            name: '',
            time: ''
        }
    }

    async addTimeSlot() {
        if (this.state.name !== '' && this.state.time !== '') {
            await fetch(`http://localhost:3001/timeslots`, {
                method: 'POST',
                headers: {
                    'Access-Control-Allow-Origin': '*',
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ 
                    name: this.state.name,
                    time: this.state.time
                })
            })
        }
    }

    handleChange(e) {
        const inputName = e.target.getAttribute('name');

        this.setState({
            [inputName]: e.target.value
        });
    }
    
    render() {
        return (
            <div class="add-time-slot">
                <div class="add-time-slot__slots">
                    <div class="add-time-slot__input-wrapper">
                        <label class="add-time-slot__label" htmlFor="name">Namn</label>
                        <input class="add-time-slot__input" type="text" name="name" id="name" onChange={e => this.handleChange(e)} />
                    </div>
                    
                    <div class="add-time-slot__input-wrapper">
                        <label class="add-time-slot__label" htmlFor="time">Tid</label>
                        <input class="add-time-slot__input" type="text" name="time" id="time" onChange={e => this.handleChange(e)} />
                    </div>
                </div>

                <button class="add-time-slot__btn" onClick={e => this.addTimeSlot()}>Lägg till</button>
            </div>
        )
    }
}   

export default AddTimeSlot;