import { h, render, Component } from 'preact';

import { TimeSlot } from '../../components';

class TimeSlotsList extends Component {
    constructor(props) {
        super(props);

        this.state = {
            timeSlots: []
        }
    }

    componentDidMount() {
        this.getTimeSlots();
    }

    async getTimeSlots() {
        const response = await fetch('http://localhost:3001/timeslots', {
            'Access-Control-Allow-Origin': '*',
            'Accept': 'application/json'
        })

        const timeSlots = await response.json();

        this.setState({
            timeSlots: timeSlots
        });
    }

    async updateTimeSlot(id, client) {
        await fetch(`http://localhost:3001/timeslots/${id}`, {
            method: 'POST',
            headers: {
                'Access-Control-Allow-Origin': '*',
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ client: client })
        })

        this.getTimeSlots();
    }

    render() {
        return (
            <div className="time-slots-list">
                {this.state.timeSlots !== [] ? (
                    this.state.timeSlots.map(timeSlot => {
                        return (
                            <TimeSlot {...timeSlot} updateTimeSlot={(id, client) => this.updateTimeSlot(id, client)} />
                        )
                    })
                ) : (
                        <p class="time-slots-list__empty-msg">Inga tider inlagda</p>
                    )}
            </div>
        )
    }
}

export default TimeSlotsList;